import Vue from "vue";
import Vuex from "vuex";
import Axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    photos: [],
    page: 1
  },
  mutations: {
    GET_PHOTOS: (state, payload) => {
      state.photos.push(...payload);
    },
    INCREMENT_PAGE: state => {
      state.page += 1;
    }
  },
  actions: {
    GET_PHOTOS: context => {
      Axios.get("https://api.unsplash.com/photos", {
        params: {
          client_id:
            "fb9b0cd6e8773dbe69d55cbd06c010d2697c99af3de328820c96289f31cc8d37",
          per_page: 20,
          page: context.state.page
        }
      })
        .then(res => {
          if (res.status === 200) {
            context.commit("GET_PHOTOS", res.data);
            context.commit("INCREMENT_PAGE");
          } else {
            console.log(res);
          }
        })
        .catch(err => {
          console.error(err);
        });
    }
  }
});
